import React from "react";
import { Button, Grid } from "@mui/material";
import { useNavigate } from "react-router-dom";

const MainPage = () => {
  const navigate = useNavigate();

  return (
    <Grid>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/linear-equation");
          }}
        >
          Rwnanie liniowe
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/square-equation");
          }}
        >
          Rwnanie kwadratowe
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/points-distance");
          }}
        >
          Odlego midzy punktami
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/arithmetic-average");
          }}
        >
          rednia arytmetyczna
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/weighted-average");
          }}
        >
          rednia waona
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/bubble-sort");
          }}
        >
          Sortowanie bbelkowe
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/quick-sort");
          }}
        >
          Sortowanie szybkie
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/calculator");
          }}
        >
          Kalkulator
        </Button>
      </Button>
      <Button>
        <Button
          fullWidth
          variant="contained"
          onClick={() => {
            navigate("/triangle-calculations");
          }}
        >
          Trjkt prostoktny- boki i kty
        </Button>
      </Button>
    </Grid>
  );
};

export default MainPage;
