import React from "react";
import { useState } from "react";
import {
  Grid,
  TextField,
  TextareaAutosize,
  Button,
  Typography,
  IconButton,
  Checkbox,
  FormControlLabel,
} from "@mui/material";
import Validator from "../../validators/validator";
import BubbleSortService from "../../services/sorting/bubbleSortService";
import { OverlayTrigger, Popover } from "react-bootstrap";
import InfoIcon from "@mui/icons-material/Info";

export interface Numbers {
  allNumbers: string;
  ascending: string;
}

export interface NewNumbers {
  newWrittenNumbers: string;
}

export interface Distance {
  solution: string;
  hint: string;
}

export interface NumberError {
  numbersError: string;
}

const BubbleSort = () => {
  const [numbers, setNumbers] = useState<Numbers>({
    allNumbers: "",
    ascending: "yes",
  });
  const [newNumbers, setNewNumbers] = useState<NewNumbers>({
    newWrittenNumbers: "",
  });
  const [res, setRes] = useState<Distance>({
    solution: "",
    hint: "",
  });

  const [errors, setErrors] = useState<NumberError>({
    numbersError: "",
  });

  const [hint, setHint] = useState<boolean>(false);

  const [checked, setChecked] = useState<boolean>(true);

  const showOrHideHint = () => {
    setHint(!hint);
  };

  const [pop, setPop] = useState<boolean>(false);

  const setPopover = (val: boolean) => {
    setPop(val);
  };

  const addNumbers = () => {
    let newErrorState = { ...errors };
    newErrorState.numbersError = "";
    let valid = true;
    if (!Validator.checkIfIsNotEmpty(newNumbers.newWrittenNumbers)) {
      newErrorState.numbersError = Validator.notNumbersMessage;
      valid = false;
    } else if (
      !Validator.checkIfIsValidListOfNumbers(newNumbers.newWrittenNumbers)
    ) {
      newErrorState.numbersError = Validator.notValidListOfNumbersMessage;
      valid = false;
    }
    setErrors(newErrorState);
    if (valid === true) {
      setNewNumbers({ ...newNumbers, newWrittenNumbers: "" });
      let temp = numbers.allNumbers;
      if (temp.length > 0) {
        temp = temp + "," + newNumbers.newWrittenNumbers;
        setNumbers({ ...numbers, allNumbers: temp });
      } else {
        setNumbers({ ...numbers, allNumbers: newNumbers.newWrittenNumbers });
      }
    }
  };

  const bubble_sort = () => {
    if (numbers.allNumbers && numbers.allNumbers.length > 0) {
      BubbleSortService.bubble_sort(numbers)
        .then((response) => {
          setRes(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      setErrors({ ...errors, numbersError: "Dodaj liczby najpierw" });
    }
  };

  const clearList = () => {
    setNumbers({ ...numbers, allNumbers: "" });
  };

  const handleCheckboxChange = () => {
    if (checked === true) {
      setNumbers({ ...numbers, ascending: "no" });
      setChecked(false);
    } else {
      setNumbers({ ...numbers, ascending: "yes" });
      setChecked(true);
    }
  };

  const popover = (
    <Grid marginLeft={2}>
      {pop && (
        <Popover
          id="popover-basic"
          onClick={() => {
            setPopover(false);
          }}
          onMouseLeave={() => {
            setPopover(false);
          }}
        >
          <Popover.Header as="h3" color="#cccccc">
            Jak doda liczby?
          </Popover.Header>
          <Popover.Body>
            Liczby naley wpisywa, rozdzielajc je
            <br />
            przecinkiem. Liczby te pojawi si w polu
            <br />
            "Twoje liczby". Maksymalnie mona doda 30 liczb.
            <br />
            Aby posortowa liczby, naley klikn
            <br />
            przycisk "Sortuj". Przykadowa lista liczb:
            <br />
            5,7,9,12,15.25
            <br />
            3.5,4,7,8,12
            <br />
            -6
            <br />
            5.5,6,3,4
          </Popover.Body>
        </Popover>
      )}
    </Grid>
  );

  return (
    <Grid
      container
      justifyContent="center"
      display="flex"
      alignItems="center"
      marginTop={2}
    >
      <Grid item width={1000}>
        <Grid
          item
          justifyContent="center"
          alignItems="center"
          paddingBottom={2}
          marginTop={0}
          paddingTop={0}
        >
          <Typography fontSize={40} color="#3760FA" textAlign="center">
            Sortowanie bbelkowe
          </Typography>
        </Grid>
        <Grid item width={1000} display="flex">
          <Grid item width={790}>
            <TextField
              fullWidth
              placeholder="Wpisz liczby.."
              value={
                newNumbers.newWrittenNumbers ? newNumbers.newWrittenNumbers : ""
              }
              onChange={(event) =>
                setNewNumbers({
                  ...newNumbers,
                  newWrittenNumbers: event.target.value,
                })
              }
              helperText={errors.numbersError ? errors.numbersError : ""}
            ></TextField>
          </Grid>
          <Grid item width={150} marginLeft={2} marginTop={1}>
            <Button
              fullWidth
              variant="contained"
              onClick={() => {
                addNumbers();
              }}
            >
              Dodaj liczby
            </Button>
          </Grid>
          <Grid display="flex">
            <Typography fontSize={36}>&nbsp;</Typography>
            <OverlayTrigger
              trigger="click"
              rootClose
              placement="right"
              overlay={popover}
            >
              <IconButton
                onClick={() => {
                  setPopover(true);
                }}
              >
                <InfoIcon />
              </IconButton>
            </OverlayTrigger>
          </Grid>
        </Grid>
        <Grid item marginTop={2}>
          <Grid item>
            <Typography fontSize={16} color="#2C74B3" textAlign="left">
              Twoje liczby
            </Typography>
          </Grid>
          <Grid item display="flex">
            <Grid item>
              <TextareaAutosize
                value={numbers.allNumbers ? numbers.allNumbers : ""}
                style={{ width: 780, resize: "vertical" }}
              />
            </Grid>
            <Grid item width={150} marginLeft={2}>
              <Button
                fullWidth
                variant="contained"
                onClick={() => {
                  clearList();
                }}
              >
                Wyczy
              </Button>
            </Grid>
          </Grid>
          <Grid item>
            <FormControlLabel
              control={
                <Checkbox checked={checked} onChange={handleCheckboxChange} />
              }
              label="Rosnco"
            />
          </Grid>
          <Grid item marginTop={2}>
            <Button
              fullWidth
              variant="contained"
              onClick={() => {
                bubble_sort();
              }}
            >
              Sortuj
            </Button>
          </Grid>
        </Grid>
      </Grid>
      <Grid item marginTop={3}>
        <Grid item>
          <Typography fontSize={16} color="#2C74B3" textAlign="left">
            Wynik
          </Typography>
        </Grid>
        <TextareaAutosize
          value={res.solution ? res.solution : ""}
          style={{ width: 1000, resize: "vertical" }}
        />
      </Grid>
      <Grid item marginTop={3} width={1000}>
        <Button
          style={{
            backgroundColor: "#21b6ae",
          }}
          fullWidth
          variant="contained"
          onClick={() => {
            showOrHideHint();
          }}
        >
          Poka wskazwk
        </Button>
      </Grid>
      <Grid item marginTop={2}>
        {hint && (
          <Grid item>
            <Grid item>
              <Typography fontSize={16} color="#2C74B3" textAlign="left">
                Wskazwka
              </Typography>
            </Grid>
            <TextareaAutosize
              value={res.hint ? res.hint : ""}
              style={{ width: 1000, resize: "vertical" }}
            />
          </Grid>
        )}
      </Grid>
    </Grid>
  );
};

export default BubbleSort;
