import axios from "axios";

export interface Point {
  x1: string;
  y1: string;
  x2: string;
  y2: string;
}

class PointService {
  private url1 = `https://calcfree.azurewebsites.net`;
  findDistanceBetweenPoints(point: Point) {
    return axios.get(`${this.url1}/points-distance/`, {
      params: {
        firstPoint: point.x1.trimStart() + "," + point.y1.trimStart(),
        secondPoint: point.x2.trimStart() + "," + point.y2.trimStart(),
      },
    });
  }
}

export default new PointService();
