class Validator {
  emptyMessage = "Nie wpisano rwnania";
  notMinLengthMessage = "Rwnanie nie zawiera wszystkich wymaganych znakw";
  linearEquationStartsFromZeroMessage =
    "Rwnanie majce parametr 0 przy x nie jest liniowe";
  quadraticEquationStartsFromZeroMessage =
    "Rwnanie majce parametr 0 przy x^2 nie jest kwadratowe";
  invalidEquationMessage = "Wpisane rwnanie ma niepoprawny format";
  emptyX = "Wpisz warto wsprzdnej x";
  emptyY = "Wpisz warto wsprzdnej y";
  notNumberMessage = "Wpisana warto nie jest liczb";
  notNumbersMessage = "Nie wpisano adnej liczby";
  notValidListOfNumbersMessage = "Wpisz poprawnie list liczb";

  checkIfIsNotEmpty = (text: string) => {
    if (text.length > 0) {
      return true;
    }
    return false;
  };

  checkMinimumLength = (text: string, min_length: number) => {
    if (text.length >= min_length) {
      return true;
    }
    return false;
  };

  checkIfIsValidLinearEquation = (text: string) => {
    console.log(text.split(" ").join(""));
    return /^(-?(\d+(\.\d+)?)?)?x([+-]\d+(\.\d+)?)?$/.test(
      text.split(" ").join("")
    );
  };
  checkIfIsValidSquareEquation = (text: string) => {
    console.log(text.split(" ").join(""));
    return /^(-?(\d+(\.\d+)?)?)?x\^2(([+-](\d+(\.\d+)?)?)?x)?([+-]\d+(\.\d+)?)?$/.test(
      text.split(" ").join("")
    );
  };
  checkNotContainsZeroOnStart = (text: string) => {
    return !/^-?0(\.0+)?x/.test(text.split(" ").join(""));
  };
  checkIfIsValidNumber = (text: string) => {
    return /^-?\d+(\.\d+)?$/.test(text.trimStart());
  };
  checkIfIsValidListOfNumbers = (text: string) => {
    return /^(-?\d+(\.\d+)?,)*(-?\d+(\.\d+)?)$/.test(text.split(" ").join(""));
  };
}

export default new Validator();
